import { t } from '../translation';

export default function validateServerPagination(
  v: unknown,
  serverPagination: boolean,
  max: number,
) {
  if (Number(v) > +max && !serverPagination) {
    return t('Server pagination needs to be enabled for values over %s', max);
  }
  return false;
}
